.page {
  scroll-behavior: smooth;
  color: #333;
  max-width: 1440px;
  margin-inline: auto;
  font-family: Inter;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  background-color: #fff;
  width: 100vw;
  height: 100vh;
  padding-block: 25px;
  transition: all .3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__language {
  color: #828282;
  font-weight: 300;
  text-decoration: none;
  transition: all .3s;
}

@media (width >= 1280px) {
  .page__language:hover {
    color: inherit;
  }
}

input:-webkit-autofill {
  -webkit-text-fill-color: #bdbdbd;
  -webkit-box-shadow: inset 0 0 0 40rem #fff;
}

input:-webkit-autofill:hover {
  -webkit-text-fill-color: #bdbdbd;
  -webkit-box-shadow: inset 0 0 0 40rem #fff;
}

input:-webkit-autofill:focus {
